<template>
    <div>
        <VueLoading v-if="!loaded"></VueLoading>
        <div v-if="loaded">
            <div style="margin-bottom: 10px">
                <el-row>
                    <el-col :span="6">
                        <el-input placeholder="search NO." v-model="filters[0].value"></el-input>
                    </el-col>
                </el-row>
            </div>

            <data-tables :data="data" :action-col="actionCol" :filters="filters" @selection-change="handleSelectionChange">
                <el-table-column type="selection" width="55">
                </el-table-column>

                <el-table-column v-for="title in titles" :prop="title.prop" :label="title.label" :key="title.prop" sortable="custom">
                </el-table-column>
            </data-tables>
        </div>
    </div>
</template>

<script>
    // fake server
    import VueLoading from '../components/VueLoading';
    let serverData = []

    for (let i = 0; i < 1000; i++) {
        serverData.push({
            'content': 'Lock broken' + i,
            'flow_no': 'FW20160101000' + i,
            'flow_type': i % 2 === 0 ? 'Repair' : 'Help',
            'flow_type_code': i % 2 === 0 ? 'repair' : 'help',
        })
    }

    let mockServer = function (res) {
        let datas = serverData.slice()
        let allKeys = Object.keys(data[0])

        // do filter
        res && res.filters && res.filters.forEach(filter => {
            datas = datas.filter(data => {
                let props = (filter.search_prop && [].concat(filter.search_prop)) || allKeys
                return props.some(prop => {
                    if (!filter.value || filter.value.length === 0) {
                        return true
                    }
                    return [].concat(filter.value).some(val => {
                        return data[prop].toString().toLowerCase().indexOf(val.toLowerCase()) > -1
                    })
                })
            })
        })

        // do sort
        if (res.sort && res.sort.order) {
            let order = res.sort.order
            let prop = res.sort.prop
            let isDescending = order === 'descending'

            datas.sort(function (a, b) {
                if (a[prop] > b[prop]) {
                    return 1
                } else if (a[prop] < b[prop]) {
                    return -1
                } else {
                    return 0
                }
            })
            if (isDescending) {
                datas.reverse()
            }
        }

        return {
            data: datas.slice((res.page - 1) * res.pageSize, res.page * res.pageSize),
            req: res,
            ts: new Date(),
            total: datas.length
        }
    }

    let i = 1

    // fake http
    function http(res, time = 200) {
        return new Promise((resolve, reject) => {
            setTimeout(_ => {
                var data = mockServer(res)
                console.log('fake server return data: ', data)
                resolve(data)
            }, time)
        })
    }

    export default {
        name: "ManageUsers",
        components: {
            VueLoading
        },
        data() {
            return {
                loaded: false,
                data: [],
                titles: [
                    {
                        prop: "Id",
                        label: "User Id"
                    },
                    {
                        prop: "Username",
                        label: "Username"
                    },
                    {
                        prop: "FirstName",
                        label: "First Name"
                    },
                    {
                        prop: "LastName",
                        label: "Last Name"
                    },
                    {
                        prop: "IsLocked",
                        label: "Is Locked"
                    },
                    {
                        prop: "IsActive",
                        label: "Is Active"
                    },
                    {
                        prop: "Id",
                        label: ''
                    }
                ],
                filters: [
                    {
                        prop: 'Username',
                        value: ''
                    },
                    {
                        prop: 'FirstName',
                        value: ''
                    },
                    {
                        prop: 'LastName',
                        value: ''
                    }
                ],
                actionCol: {
                    props: {
                        label: 'Actions',
                    },
                    buttons: [
                        {
                            props:
                            {
                                type: 'primary'
                            },
                            handler: row =>
                            {
                                this.$message('Edit clicked')
                            },
                            label: 'Edit'
                        },
                        {
                            handler: row => {
                                this.data.splice(this.data.indexOf(row), 1)
                                this.$message('Deactivate pressed')
                            },
                            label: 'Deactivate'
                        }
                    ]
                },
                selectedRow: []
            }
        },
        mounted: function () {
            this.loaded = false;
            let baseUrl = "http://127.0.0.1:5000"
            let userUrl = baseUrl + "/api/getUsers";
            var that = this;
            axios.get(userUrl).then(function (response) {
                that.data = JSON.parse(response.data);
                console.log(response.data)
            }).catch(function (error) {
                console.log(error);
                this.$emit('error', true)
            });
            that.loaded = true;
        },
        methods: {
            handleSelectionChange(val) {
                this.selectedRow = val
            },
        }
    }
</script>